.modal-enter {
  opacity: 0;
}

.modal-enter.modal-enter-active {
  opacity: 1;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}

.modal-leave {
  opacity: 1;
}

.modal-leave.modal-leave-active {
  opacity: 0;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}

.product-enter {
  opacity: 0;
}

.product-enter.product-enter-active {
  opacity: 1;
  -webkit-transition: all 1500ms ease-in;
  -o-transition: all 1500ms ease-in;
  transition: all 1500ms ease-in;
}

.product-leave {
  opacity: 1;
}

.product-leave.product-leave-active {
  opacity: 0;
}

.loader-enter {
  opacity: 1;
}

.loader-enter.loader-enter-active {
  opacity: 1;
  -webkit-transition: opacity 4000ms ease-in;
  -o-transition: opacity 4000ms ease-in;
  transition: opacity 4000ms ease-in;
}

.loader-leave {
  opacity: 1;
}

.loader-leave.loader-leave-active {
  opacity: 0;
  -webkit-transition: opacity 600ms ease-in;
  -o-transition: opacity 600ms ease-in;
  transition: opacity 600ms ease-in;
}

.message-enter {
  opacity: 0;
  -webkit-transform: translateX(-50px);
      -ms-transform: translateX(-50px);
          transform: translateX(-50px);
}

.message-enter.message-enter-active {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  -webkit-transition: all 1000ms ease-in;
  -o-transition: all 1000ms ease-in;
  transition: all 1000ms ease-in;
}

.message-leave {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.message-leave.message-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-50px);
      -ms-transform: translateX(-50px);
          transform: translateX(-50px);
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}



